import { useState, useEffect } from 'react';
import { GrClose } from "react-icons/gr";
import { HiOutlineChevronLeft } from "react-icons/hi";

const Slide3Page1Code = props => {
    const {
        amountToPay,
        calculateAmountToPay,
        deductAmountToPay,
        setPageStack,
        pageStack,
    } = props;

    return (
        <div style={{
            width: '100%',
            height: '100%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column',
            backgroundColor: '#FECA3E',
        }}>
            <div style={{
                position: 'absolute',
                left: 15,
                top: 20,
            }} onClick={() => {
                setPageStack([
                    ...pageStack,
                    'Slide2Page3Code',
                ]);
            }}>
                <GrClose size={26} />
            </div>

            <div style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexDirection: 'column',
            }}>
                <span style={{
                    fontFamily: 'NunitoSans-Bold',
                    fontSize: 18,
                    marginBottom: 20,
                }}>Enter Amount</span>

                <span style={{
                    fontFamily: 'NunitoSans-Bold',
                    fontSize: 48,
                    marginBottom: 16,
                }}>{`RM ${amountToPay.toFixed(2)}`}</span>

                <div style={{
                    width: window.screen.width * 0.6,
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'space-between',
                }}>
                    <div style={{
                        width: '30%',
                        display: 'flex',
                        flexDirection: 'column',
                    }}>
                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(1);
                        }}>1</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(4);
                        }}>4</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(7);
                        }}>7</span>

                        <div style={{
                            borderRadius: '50%',
                            border: '1.5px solid #000',
                            width: 48,
                            height: 48,
                            borderColor: 'black',
                            alignSelf: 'center',
                            marginTop: 6,
                            marginBottom: 12,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }} onClick={() => {
                            deductAmountToPay();
                        }}>
                            <div style={{
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',

                                paddingRight: 1,
                            }}>
                                <HiOutlineChevronLeft size={24} />
                            </div>
                        </div>
                    </div>

                    <div style={{
                        width: '30%',
                        display: 'flex',
                        flexDirection: 'column',
                    }}>
                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(2);
                        }}>2</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(5);
                        }}>5</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(8);
                        }}>8</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(0);
                        }}>0</span>
                    </div>

                    <div style={{
                        width: '30%',
                        display: 'flex',
                        flexDirection: 'column',
                    }}>
                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(3);
                        }}>3</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(6);
                        }}>6</span>

                        <span style={{
                            marginTop: 12,
                            marginBottom: 12,
                            fontFamily: 'NunitoSans-Regular',
                            fontSize: 28,
                        }} onClick={() => {
                            calculateAmountToPay(9);
                        }}>9</span>

                        <div style={{
                            borderRadius: '50%',
                            border: '1.5px solid #000',
                            width: 48,
                            height: 48,
                            borderColor: 'black',
                            alignSelf: 'center',
                            marginTop: 6,
                            marginBottom: 12,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }} onClick={() => {
                            setPageStack([
                                ...pageStack,
                                'Slide3Page2Code',
                            ]);
                        }}>
                            <span style={{
                                fontFamily: 'NunitoSans-Bold',
                                fontSize: 15,
                            }}>OK</span>
                        </div>
                    </div>
                </div>
            </div>

            <div style={{
                height: '10%',
                width: '100%',
            }}>

            </div>
        </div >
    )
};

export default Slide3Page1Code;